  transition: all 8s ease 4s;
  user-select: none; }

@keyframes jumping {
  0% {
    transform: translateY(0px) translateX(0) rotate(0) scale(1);
    opacity: .8; }
  25% {
    transform: translateY(-10px) translateX(-10px) rotate(20deg) scale(0.8);
    opacity: .9; }
  50% {
    transform: translateY(-15px) translateX(-15px) rotate(10deg) scale(0.9);
    opacity: .8; }
  75% {
    transform: translateY(-20px) translateX(-20px) rotate(20deg) scale(0.75);
    opacity: .6; }
  85% {
    transform: translateY(-25px) translateX(-25px) rotate(20deg) scale(0.9);
    opacity: .7; }
  100% {
    transform: translateY(-15px) translateX(-15px) rotate(0) scale(0.95);
    opacity: .85; } }

@keyframes jumping2 {
  0% {
    transform: translateY(0px) translateX(0) rotate(0) scale(1);
    opacity: .5; }
  25% {
    transform: translateY(-30px) translateX(10px) rotate(20deg) scale(0.8);
    opacity: .8; }
  50% {
    transform: translateY(15px) translateX(-15px) rotate(10deg) scale(0.7);
    opacity: .8; }
  75% {
    transform: translateY(30px) translateX(20px) rotate(90deg) scale(0.75);
    opacity: .7; }
  100% {
    transform: translateY(-15px) translateX(15px) rotate(0) scale(0.75);
    opacity: .9; } }

@keyframes jumping3 {
  0% {
    transform: translateY(10px) translateX(0) rotate(0) scale(1);
    opacity: .9; }
  20% {
    transform: translateY(20px) translateX(10px) rotate(-20deg) scale(0.8);
    opacity: .8; }
  40% {
    transform: translateY(15px) translateX(-15px) rotate(10deg) scale(0.75);
    opacity: .8; }
  40% {
    transform: translateY(-15px) translateX(-25px) rotate(10deg) scale(0.5);
    opacity: 1; }
  80% {
    transform: translateY(-30px) translateX(20px) rotate(-20deg) scale(0.75);
    opacity: .6; }
  100% {
    transform: translateY(15px) translateX(15px) rotate(0) scale(0.95);
    opacity: .7; } }

@keyframes jumping4 {
  0% {
    transform: translateY(-30px) translateX(40px) rotate(0) scale(1.2);
    opacity: .7; }
  25% {
    transform: translateY(-20px) translateX(0px) rotate(50deg) scale(0.6);
    opacity: .8; }
  50% {
    transform: translateY(15px) translateX(-55px) rotate(20deg) scale(0.5);
    opacity: .9; }
  75% {
    transform: translateY(30px) translateX(30px) rotate(50deg) scale(0.75);
    opacity: .7; }
  100% {
    transform: translateY(-15px) translateX(5px) rotate(0) scale(0.5);
    opacity: .9; } }

@keyframes jumping5 {
  0% {
    transform: rotate(180deg);
    display: block; }
  100% {
    transform: rotate(30deg);
    display: block; } }

@keyframes jumping6 {
  0% {
    transform: rotate(180deg) translate(-50px, 20px);
    display: block; }
  100% {
    transform: rotate(30deg) translate(0px, 0px);
    display: block; } }

